











import { Component, Vue } from 'vue-property-decorator';
import Platform from '@/utils/platform';

@Component
export default class JumpIndex extends Vue {
  private ifWeixin: boolean = false;
  private platform: string = '';

  private created(): void {
    const platform = new Platform();
    this.ifWeixin = platform.checkWeixin();
    this.platform = platform.checkPlatform();
  }
  private mounted(): void {
    const that = this;
    if (!this.ifWeixin) {
      window.location.href = 'zuber:/' + this.$route.query.jumpUrl;
      setTimeout(() => {
        that.$router.push({
          name: 'download',
          query: {
            jumpUrl: that.$route.query.jumpUrl,
          },
        });
      }, 4000);
    }
  }
}
